html,
body,
#root {
    width: 100%;
    min-height: 100vh;
    margin: 0;
    padding: 0;
}

.container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    min-height: 100vh;
}

.header {
    margin: 0;
    padding: 40px;
    width: 100%;
}

.content {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex: 1 1;
        flex: 1 1;
    width: 960px;
    max-width: 90%;
}

.text-center {
    text-align: center;
}

@media screen and (max-width: 840px) {
    body {
        font-size: 70%;
    }
}


